@import url(https://fonts.googleapis.com/css2?family=Archivo:wght@300&family=Patrick+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: linear-gradient(130deg, #f34646, #642cbfcc);
}

p {
  font-family: 'Patrick Hand', cursive;
  font-size: 20px;
  font-weight: 100;
}

h1, h2, h3, h4, h5 {
  font-family: 'Archivo', sans-serif;
}

li {
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
}

.app {
  border-bottom: 20px solid #a851a0;
}

.text-input {
  width: 50%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white
}

.eventListAll {
  width: 50%;
  text-align: center;
  height: 889px;
  overflow-y: scroll;
}

.event {
  margin-bottom: 40px;
  box-shadow: 16px 6px 16px;
}

.overlayInfo {
  background: #000000c2;
  width: 100%;
  position: absolute;
  left: 0;
  height: 100%;
  color: white;
  z-index: 3;
  display: flex;
}

.hideOverlayInfo {
  display: none;
}

.overlayHeader {
  transition-duration: .5s;
  width: 50%;
  text-align: center;
  position: absolute;
  right: 25%;
  bottom: 40%;
}

.overlayCloseButton {
  position: absolute;
  right: 10%;
  bottom: 94%;
}

.onExitButton {
  cursor: pointer;
  color: red;
}

.onExitButton:hover {
  color: white;
}

.onExitButton:active {
  color: blue;
}

@media only screen and (max-width: 992px) {
  .text-input {
    background-color: #f5f5f5e0;
    color: black;
    position: absolute;
    left: 0;
    z-index: 1;
    width: 100%;
    padding: 14px;
    flex-direction: row;
    border-radius: 5px;
    box-shadow: 2px 2px 10px;
    display: flex;
    align-items: center;
  }

  .eventListAll {
    width: 100%;
    z-index: 0;
  }
  
  .EventList {
    position: relative;
    top: 20%;
  }

  .overlayHeader {
    width: 95%;
    text-align: center;
    position: absolute;
    right: 2%;
    bottom: 30%;
  }
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@keyframes nprogress-spinner {
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.CitySearch {
  padding: 20px;
}

.city {
  width: 65%;
  display: inline;
}

.suggestionList {
  transition-duration: .5s;
}

.suggestionList:hover {
  opacity: .5;
  cursor: pointer;
  transform: scale(1.1);
}

.suggestion {
  transition-duration: 1s;
}
.num-of-events {
  width: 65%;
  display: inline;
}
